<template>
  <div class="list">
    <div class="order-box">
      <div class="order-title"> {{language.market_order_record}}</div>
      <div class="head">
        <div
          :class="orderChang ? 'on-sale' : 'completed'"
          @click="saleCompFn(false)"
        >
          {{language.for_sale}}
        </div>
        <div
          :class="orderChang ? ' completed' : 'on-sale'"
          @click="saleCompFn(true)"
        >
           {{language.completed_c}}
        </div>
      </div>
    </div>

    <div class="orderbox" v-if="!orderChang">
       <div v-if="onSaleList.length>0">
              <div class="" v-for="(item, index) in onSaleList" :key="index">
               <orderList :listStatus="true" :defxToken="item.defxToken"  :item="item"  @cancelSaleFn="cancelSaleFn"> </orderList>
              </div>
        </div>
          <ul v-else>
            <li class=" "  v-show="!loading&&onSaleList.length===0">
              <p class="text fs-16 ta-center">
                <img src="@/images/no-data.png" alt="nodata"><br>{{ language.no_data }}
              </p>
            </li>
          </ul>
          <!-- <div v-if="loading" class="mask flex jc-center ai-center ta-center">
            <span><i class="icon-loading"></i><br>{{ language.tip_loading }}...</span>
          </div> -->
    </div>

    <div class="orderbox" v-if="orderChang">
      <div v-if="onSaleList.length>0">
          <div class="" v-for="(item, index) in onSaleList" :key="index">
            <orderList :listStatus="false" :item="item" > </orderList>
          </div>
      </div>
        <ul v-else>
            <li class=" "  v-show="!loading&&onSaleList.length===0">
              <p class="text fs-16 ta-center">
                <img src="@/images/no-data.png" alt="nodata"><br>{{ language.no_data }}
              </p>
            </li>
         </ul>
    </div>
  </div>
</template>
<script>
import orderList from "./popUpLayer/orderList";
import { mapGetters } from "vuex";
export default {
  data() {
    return {
      searchText: "",
      hedged: false,
      orderChang: false,
      listStatus: false,
      onSaleList:[],
     loading: false, //加载中
    };
  },
  components: {
    orderList,
  },
  computed: {
    ...mapGetters(["language"]),
  },
  mounted() {
     this.initOnSale();
  },
  methods: {
    // 出售中
    initOnSale(){
       if (this.loading) return;
       this.$api.querymarketLsitOnSale().then((res)=>{
            this.onSaleList =  res.salesObjects;
            this.onSaleList.forEach( (element )=> {
                 element.price = this.getSalesPrice(element);
             });
             console.log( this.onSaleList,8888)
          }).catch((error) => {
        console.error(error)
        this.loading = false;
      })
    },
    // 已完成
      completedInit(){
       this.$api.completedInitFn().then((res)=>{
            this.onSaleList =  res.salesObjects;
            this.onSaleList.forEach( (element )=> {
                 element.price = this.getSalesPrice(element);
             });
             console.log( this.onSaleList,8888)
          })  .catch((error) => {
        console.error(error)
        this.loading = false;
      }) 
    },
     getSalesPrice(obj) {
        let timeSeconds = Date.parse(new Date()) / 1000;
        if( obj.status == 1) {
            return Number(obj.finalPrice) ;
        } else {
            if((Number(obj.startTime)  + Number(obj.durationTime) ) < Number(timeSeconds)) {
                return  Number(obj.minPrice)
            } else if ( Number(obj.startTime)  >= Number(timeSeconds) ) {
                return  Number( obj.maxPrice);
            } else {
                let  per = ( Number(obj.maxPrice)  - Number(obj.minPrice)) / Number(obj.durationTime);
                return Number(obj.maxPrice) - (Number(timeSeconds) - Number(obj.startTime)) * Number(per);
            }
        }
    },
    // 子组件方法
     cancelSaleFn(){
          this.initOnSale();
          this.completedInit();
     },
    saleCompFn(type) {
      this.orderChang = type;
      if(type){
         this.completedInit();
      }else{
          this.initOnSale();
      }
    },
  },
};
</script>
